    </div>
    <button @click="sorting">ÎÇÆÏùÄÍ∞ÄÍ≤©ÏàúÏ†ïÎ†¨</button>
    <button @click="sortBack">ÏõêÎûòÎåÄÎ°ú</button>
    <Card @openModal="modalData=true; clickData = $event" :oneroom="oneroom" :modalData="modalData" :close="close"/>
  </div>
</template>

<script>

import data from './assets/oneroom.js';
import Modal from './components/Modal.vue';
import Card from './components/Card.vue';

export default {
  data(){
    return {
      oneroomOrg : [...data],
      objectData : { name : 'kim', age: 20},
      oneroom : data,
      clickData : 0,
      modalData : false
    }
  },
  components: {
    Modal : Modal,
    Card : Card
  },
  methods : {
    close : function(){
      this.modalData = false;
    },
    sorting : function() {
      this.oneroom.sort(function(a,b){
        return a.price-b.price
      })
    },
    sortBack : function (){
      this.oneroom = [...this.oneroomOrg];
    }
  }
}
</script>

